/**
 *  
 */
<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-form :inline="true" :model="formInline" class="user-search">
      <el-form-item label="">
        <el-select v-model="formInline.isLock" placeholder="">
          <el-option label="" value=""></el-option>
          <el-option label="" value="N"></el-option>
          <el-option label="" value="Y"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="">
        <el-input v-model="formInline.userName" placeholder=""></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-input v-model="formInline.userMobile" placeholder=""></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" icon="el-icon-search" @click="search"></el-button>
        <el-button type="primary" icon="el-icon-plus" @click="handleEdit()"></el-button>
        <el-button type="primary" @click="handleunit()"></el-button>
      </el-form-item>
    </el-form>
    <!---->
    <el-table @selection-change="selectChange" :data="userData" highlight-current-row v-loading="loading" border element-loading-text="" style="width: 100%;">
      <el-table-column align="center" type="selection" width="50">
      </el-table-column>
      <el-table-column align="center" sortable prop="deptName" label="" width="120">
      </el-table-column>
      <el-table-column align="center" sortable prop="userName" label="" width="120">
      </el-table-column>
      <el-table-column align="center" sortable prop="userRealName" label="" width="120">
      </el-table-column>
      <el-table-column align="center" sortable prop="userMobile" label="" width="120">
      </el-table-column>
      <el-table-column align="center" sortable prop="userSex" label="" min-width="50">
      </el-table-column>
      <el-table-column align="center" sortable prop="userEmail" label="" min-width="120">
      </el-table-column>
      <el-table-column align="center" sortable prop="editTime" label="" min-width="120">
        <template slot-scope="scope">
          <div>{{scope.row.editTime|timestampToTime}}</div>
          </el-switch>
        </template>
      </el-table-column>
      <el-table-column align="center" sortable prop="isLock" label="" min-width="50">
        <template slot-scope="scope">
          <el-switch v-model="scope.row.isLock=='N'?nshow:fshow" active-color="#13ce66" inactive-color="#ff4949" @change="editType(scope.$index, scope.row)">
          </el-switch>
        </template>
      </el-table-column>
      <el-table-column label="" min-width="300">
        <template slot-scope="scope">
          <el-button size="mini" @click="handleEdit(scope.$index, scope.row)"></el-button>
          <el-button size="mini" type="danger" @click="deleteUser(scope.$index, scope.row)"></el-button>
          <el-button size="mini" type="success" @click="resetpwd(scope.$index, scope.row)"></el-button>
          <el-button size="mini" type="success" @click="dataAccess(scope.$index, scope.row)"></el-button>
          <el-button size="mini" type="success" @click="offlineUser(scope.$index, scope.row)"></el-button>
          <el-button size="mini" type="success" @click="refreshCache(scope.$index, scope.row)"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <!--  -->
    <Pagination v-bind:child-msg="pageparm" @callFather="callFather"></Pagination>
    <!--  -->
    <el-dialog :title="title" :visible.sync="editFormVisible" width="30%" @click='closeDialog("edit")'>
      <el-form label-width="80px" ref="editForm" :model="editForm" :rules="rules">
        <el-form-item label="" prop="userName">
          <el-input v-model="editForm.userName" auto-complete="off" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="userRealName">
          <el-input v-model="editForm.userRealName" auto-complete="off" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="roleId">
          <el-select v-model="editForm.roleId" placeholder="" class="userRole">
            <el-option label="" value="1"></el-option>
            <el-option label="" value="2"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="userMobile">
          <el-input v-model="editForm.userMobile" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="userEmail">
          <el-input v-model="editForm.userEmail" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="userSex">
          <el-radio v-model="editForm.userSex" label=""></el-radio>
          <el-radio v-model="editForm.userSex" label=""></el-radio>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click='closeDialog("edit")'></el-button>
        <el-button type="primary" :loading="loading" class="title" @click="submitForm('editForm')"></el-button>
      </div>
    </el-dialog>
    <!--  -->
    <el-dialog title="" :visible.sync="dataAccessshow" width="30%" @click='closeDialog("perm")'>
      <el-tree ref="tree" default-expand-all="" :data="UserDept" :props="defaultProps" :default-checked-keys="checkmenu" node-key="id" show-checkbox>
      </el-tree>
      <div slot="footer" class="dialog-footer">
        <el-button @click='closeDialog("perm")'></el-button>
        <el-button type="primary" :loading="loading" class="title" @click="menuPermSave"></el-button>
      </div>
    </el-dialog>
    <!--  -->
    <el-dialog title="" :visible.sync="unitAccessshow" width="30%" @click='closeDialog("unit")'>
      <el-tree ref="tree" default-expand-all="" :data="UserDept" :props="defaultProps" @check-change="handleClick" :default-checked-keys="checkmenu" node-key="id" show-checkbox check-strictly>
      </el-tree>
      <div slot="footer" class="dialog-footer">
        <el-button @click='closeDialog("unit")'></el-button>
        <el-button type="primary" :loading="loading" class="title" @click="unitPermSave"></el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
// 
