      fshow: false, //switch
      loading: false, //
      title: '',
      editFormVisible: false, //
      dataAccessshow: false, //
      unitAccessshow: false, //
      // 
      editForm: {
        userId: '',
        userName: '',
        userRealName: '',
        roleId: '',
        userMobile: '',
        userEmail: '',
        userSex: '',
        token: localStorage.getItem('logintoken')
      },
      // 
      unitparm: {
        userIds: '',
        deptId: '',
        deptName: ''
      },
      // 
      selectdata: [],
      // rules
      rules: {
        userName: [
          { required: true, message: '', trigger: 'blur' }
        ],
        userRealName: [
          { required: true, message: '', trigger: 'blur' }
        ],
        roleId: [{ required: true, message: '', trigger: 'blur' }],
        userMobile: [
          { required: true, message: '', trigger: 'blur' },
          {
            pattern: /^1(3\d|47|5((?!4)\d)|7(0|1|[6-8])|8\d)\d{8,8}$/,
            required: true,
            message: '',
            trigger: 'blur'
          }
        ],
        userEmail: [
          { required: true, message: '', trigger: 'blur' },
          {
            pattern: /^[A-Za-z0-9\u4e00-\u9fa5]+@[a-zA-Z0-9_-]+(\.[a-zA-Z0-9_-]+)+$/,
            required: true,
            message: '',
            trigger: 'blur'
          }
        ],
        userSex: [{ required: true, message: '', trigger: 'blur' }]
      },
      // 
      seletedata: {
        ids: '',
        token: localStorage.getItem('logintoken')
      },
      // 
      resetpsd: {
        userId: '',
        token: localStorage.getItem('logintoken')
      },
      // 
      offline: {
        token: localStorage.getItem('logintoken')
      },
      // 
      formInline: {
        page: 1,
        limit: 10,
        deptId: '',
        userName: '',
        userMobile: '',
        isLock: ''
      },
      //
      userData: [],
      // 
      UserDept: [],
      defaultProps: {
        children: 'children',
        label: 'name'
      },
      // 
      checkmenu: [],
      //role
      saveroleId: '',
      // 
      pageparm: {
        currentPage: 1,
        pageSize: 10,
        total: 10
      }
    }
  },
  // 
  components: {
    Pagination
  },

  /**
   * 
   */
  watch: {},

  /**
   * 
   */
  created() {
    this.getdata(this.formInline)
  },

  /**
   * 
   */
  methods: {
    // 
    getdata(parameter) {
      this.loading = true
      // 
      let res = {
        code: 0,
        msg: null,
        count: 12,
        data: [
          {
            addUser: '1',
            editUser: '1',
            addTime: null,
